/* 🔹 Contact List Container */
.contact-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* แสดง 3 คนต่อแถว */
  gap: 20px;
  width: 100%;
  max-width: 900px;
  margin: auto;
}

/* 🔹 Contact Item (รูปแบบสี่เหลี่ยม) */
.contact-item {
  background: #fff;
  padding: 20px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

/* 🔹 รูปภาพเป็นสี่เหลี่ยมจัตุรัส */
.contact-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 10px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.1);
}

/* 🔹 ชื่อ */
.contact-name {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
}

/* 🔹 Social Icons */
.contact-socials {
  display: flex;
  gap: 10px;
}

.contact-socials a {
  color: #555;
  font-size: 20px;
  transition: color 0.3s;
}

.contact-socials a:hover {
  color: #ff6600;
}

/* 🔹 Responsive: ลดจำนวนคอลัมน์ในจอเล็ก */
@media (max-width: 768px) {
  .contact-list {
    grid-template-columns: repeat(2, 1fr); /* 2 คนต่อแถว */
  }
}

@media (max-width: 480px) {
  .contact-list {
    grid-template-columns: repeat(1, 1fr); /* 1 คนต่อแถว */
  }
}
