/* 🔹 Navbar */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 15px 20px;
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  color: white;
  z-index: 1000;
}

/* 🔹 โลโก้ตรงกลาง */
.logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 24px;
  font-weight: bold;
}
.logo-img {
  width: auto;
  height: 200px;
}
/* 🔹 เมนูด้านขวา */
.nav-right {
  display: flex;
  gap: 20px;
  margin-right: 20px;
}

.nav-right li {
  list-style: none;
}

.nav-right a {
  color: white;
  text-decoration: none;
  font-size: 18px;
  transition: 0.3s;
}

.nav-right a:hover {
  color: #ffcc00;
}

/* 🔹 ปุ่มแฮมเบอร์เกอร์ */
.menu-toggle {
  display: none;
  background: none;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
  margin-right: 20px;
}

/* 🔹 เมนู Mobile แบบ Fullscreen */
.nav-mobile {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(15px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s;
}

.nav-mobile.open {
  opacity: 1;
  visibility: visible;
}

/* 🔹 ปุ่มปิดเมนู */
.close-menu {
  position: absolute;
  top: 20px;
  right: 50px;
  background: none;
  border: none;
  color: white;
  font-size: 28px;
  cursor: pointer;
}

/* 🔹 ลิงก์ในเมนู Mobile */
.nav-mobile ul {
  list-style: none;
  padding: 0;
}

.nav-mobile li {
  margin: 20px 0;
}

.nav-mobile a {
  color: white;
  font-size: 24px;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s;
}

.nav-mobile a:hover {
  color: #ffcc00;
}

/* 🔹 Responsive: แสดง Hamburger Menu */
@media (max-width: 768px) {
  .nav-right {
    display: none;
  }

  .menu-toggle {
    display: block;
  }
}
