body {
  margin: 0; /* ลบ margin เริ่มต้นของ body */
  padding: 0; /* ลบ padding เริ่มต้นของ body */
  width: 100%; /* ครอบคลุมทั้งหน้าจอ */
  height: 100%; /* ครอบคลุมทั้งหน้าจอ */
  background-image: url("../../../public/images/wallpaper.jpg");
  background-size: cover; /* ให้รูปครอบคลุมทั้งพื้นที่ */
  background-position: center; /* จัดรูปให้อยู่ตรงกลาง */
  background-repeat: no-repeat; /* ไม่ให้ภาพซ้ำ */
  background-attachment: fixed; /* ล็อกพื้นหลังไม่ให้เลื่อนตาม */
}
.carousel-blox {
  width: 100%;
  background: linear-gradient(to bottom, #ffeed4, #fff7ea);
  justify-content: center;
  display: flex;
  align-items: center;
  padding: 80px 20px 20px 20px;
}
.carousel-wrapper {
  width: 100%;
}

.carousel-item {
  height: 800px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  border-radius: 10px;
  margin: 80px 80px;
}
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.hero-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 20px;
  position: relative;
  height: 100vh;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.5);
}
.hero-section .text-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.hero-section .hero-section-img-box {
  flex: 2;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.hero-section .hero-section-img-box img {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.two-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 20px;
  position: relative;
  height: 700px;
  flex-direction: row;
  background: #fff1dd;
}

/* 🔹 กล่องหนังสือ */
.book-container {
  position: relative;
  width: 700px; /* ปรับขนาด */
  height: 320px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* 🔹 หนังสือแต่ละเล่ม */
.image {
  position: absolute;
  width: 500px;
  height: auto;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

/* 🔹 เล่มหลัง (ลดการซ้อนทับ) */
.book-back {
  transform: translate(10px, 5px);
  z-index: 1;
  opacity: 0.85;
}

/* 🔹 เล่มหน้า (ลดการซ้อนทับ) */
.book-front {
  transform: translate(10px, 5px);
  z-index: 2;
  left: -120px;
  opacity: 0.85;
}

/* 🔹 เมื่อ Hover ที่เล่มหลัง */
.book-back:hover {
  transform: translate(-16px, -8px) scale(1.05);
  z-index: 3;
  opacity: 1;
}

/* 🔹 เมื่อ Hover ที่เล่มหน้า */
.book-front:hover {
  transform: translate(-16px, -8px) scale(1.05);
  z-index: 3;
  opacity: 1;
}

.top-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  background-color: #8dbfce;
  padding: 40px;
  position: relative;
}
.top-section .text-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.top-section .image-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; /* เพิ่มเพื่อแสดงว่าคลิกได้ */
}

/* ปุ่ม */
.btn {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.btn:hover {
  background-color: #0056b3;
}

/* Modal สำหรับหนังสือ */
.book-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  position: relative;
  padding: 20px;
  border-radius: 10px;
  width: 1000px;
  height: 600px;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  cursor: pointer;
}

/* ส่วนล่าง */
.bottom-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  background-color: #7ebb9e;
  padding: 40px;
}

.bottom-section .text-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.bottom-section .image-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.image {
  max-width: 400px;
  height: auto;
  transition: transform 0.3s ease;
}
.image:hover {
  transform: scale(1.05);
}
.image-book {
  max-width: 400px;
  height: auto;
  transition: transform 0.3s ease;
  border: #000 solid 4px;
  border-radius: 10px;
  padding: 60px 0px 60px 0px;
}
.image-book:hover {
  transform: scale(1.05);
}
.bottom-section {
  height: 500px;
}
.contact-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 20px;
  position: relative;

  flex-direction: column;
  background: hsl(0, 0%, 100%);
}
.contact-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 คอลัมน์ */
  gap: 20px; /* ระยะห่างระหว่างแต่ละรายการ */
  width: 100%;
  max-width: 1000px;
}

.contact-item {
  background: #f5f5f5;
  padding: 20px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
}
.contact-item:hover {
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5);
}
.foot-section {
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding: 20px;
  position: relative;

  flex-direction: row;
  color: #fff;
  background: hsl(0, 0%, 19%);
  align-items: center;
}
.foot-section .right {
  display: grid;
  grid-auto-flow: column; /* ให้เรียงในแนวนอน */
  gap: 20px; /* ระยะห่างระหว่างไอคอน */
  margin-right: 20px;
}
.foot-section .left {
  color: #fff;
  margin-left: 20px;
}
.foot-section .right a {
  color: white; /* หรือใช้สีที่ contrast กับพื้นหลัง */
  font-size: 24px; /* ขยายไอคอนให้ชัดขึ้น */
  transition: color 0.3s ease;
}

.foot-section .right a:hover {
  color: #1da1f2; /* เปลี่ยนสีเมื่อ hover (ใช้สี X/Twitter) */
}
